.section-prices {
    &__item {
        display: block;
        position: relative;
        overflow: hidden;
        margin-bottom: 27px;

        &:hover {
            .section-prices__item__image {
                &:before {
                    transform: translate(-24px, -24px);
                    left: 34px;
                    top: 34px;
                    right: -14px;
                    bottom: -14px;

                    @include respond-to (md) {
                        transform: none;
                        left: 5px;
                        top: 5px;
                        right: 5px;
                        bottom: 5px;
                    }
                }

                img {
                    transform: translate(24px, 24px);

                    @include respond-to (md) {
                        transform: translate(15px, 15px);
                    }

                    @include respond-to (xs) {
                        transform: none;
                    }
                }
            }
        }

        &__label {
            position: absolute;
            top: -20px;
            left: -101px;
            background: $color-blue;
            font-size: 24px;
            color: #FFF;
            text-align: center;
            white-space: nowrap;
            transform: rotate(-45deg);
            padding: 70px 15px 10px;
            width: 270px;
            z-index: 2;
        }

        &__image {
            position: relative;
            padding-bottom: 48px;
            padding-right: 48px;
            margin-bottom: 8px;

            @include respond-to (md) {
                padding-bottom: 30px;
                padding-right: 30px;
            }

            @include respond-to (xs) {
                padding: 0;
            }

            &:before {
                content: '';
                position: absolute;
                bottom: 0;
                right: 0;
                left: 48px;
                top: 48px;
                border: 14px solid $color-blue;
                transition: .5s;

                @include respond-to (md) {
                    top: 30px;
                    left: 30px;
                }

                @include respond-to (xs) {
                    display: none;
                }
            }

            img {
                position: relative;
                display: block;
                width: 100%;
                z-index: 1;
                transition: .5s;
            }
        }

        &__title {
            margin-left: 48px;
            font-size: 36px;
            color: $color-blue;
            font-family: $font-secondary;

            @include respond-to (md-down) {
                margin-left: 0;
                text-align: center;
                font-size: 28px;
                line-height: 1.2;
            }

            @include respond-to (xs) {
                font-size: vw(20);
            }
        }
    }
}