.footer {
    padding-top: 60px;

    a {
        color: #000;
    }

    &__section {
        margin-bottom: 30px;

        @include respond-to (xs) {
            text-align: center;
        }
    }

    &__title {
        font-size: 24px;
        color: $color-blue;
        font-family: $font-secondary;
        margin-bottom: 0;
    }

    &__descr {
        font-size: 24px;
    }
}