.hero {
    color: #FFF;
    text-shadow: 0 5px 5px rgba(#000, .27);

    &__wrap {
        position: relative;

        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 72px;
            right: 72px;
            bottom: 45px;
            border: 22px solid $color-blue;

            @include respond-to (xs) {
                display: none;
            }
        }
    }

    &__left {
        padding: 35px 0 124px;

        @include respond-to (xs) {
            padding: 0 0 40px;
        }
    }

    &__image-ext {
        text-align: center;

        @include respond-to (md-down) {
            display: none;
        }
    }

    &__title {
        font-size: 90px;
        line-height: 1.05;
        font-family: $font-secondary;
        margin-bottom: 30px;

        @include respond-to (lg) {
            font-size: 72px;
        }

        @include respond-to (md) {
            font-size: 62px;
        }

        @include respond-to (sm) {
            font-size: 50px;
        }

        @include respond-to (xs) {
            font-size: vw(36);
            text-align: center;
        }
    }

    &__price-label {
        display: table;
        font-size: 60px;
        background: $color-danger;
        line-height: 1.2;
        padding: 0 12px;
        margin-bottom: 32px;
        text-shadow: none;

        @include respond-to (md) {
            font-size: 46px;
        }

        @include respond-to (sm) {
            font-size: 38px;
        }

        @include respond-to (xs) {
            display: table;
            margin-left: auto;
            margin-right: auto;
            font-size: vw(28);
        }
    }

    &__descr {
        font-size: 24px;

        @include respond-to (sm) {
            font-size: 20px;
        }

        @include respond-to (xs) {
            font-size: vw(14);
            text-align: center;
        }
    }
}