.header {
    background-image: url($image-bg-header);
    background-position: center top;

    @include respond-to (xs) {
        background-position: center;
        background-size: cover;
    }

    &__top {
        padding: 30px 0;

        &__logo {
            display: flex;
            align-items: center;

            @include respond-to (md-down) {
                flex-direction: column;
                align-items: flex-start;
                text-align: left;
            }

            img {
                margin-right: 46px;

                @include respond-to (lg) {
                    margin-right: 15px;
                }

                @include respond-to (md-down) {
                    margin-right: 0;
                    margin-bottom: 15px;
                }

                @include respond-to (xs) {
                    width: 90px;
                }
            }

            &:hover {
                text-decoration: none;
            }
        }

        &__logo-text {
            color: $color-blue;
            font-size: 36px;
            font-family: $font-secondary;
            line-height: 1.2;

            @include respond-to (lg-down) {
                font-size: 28px;
            }

            @include respond-to (xs) {
                font-size: vw(22);
                margin-bottom: 20px;
            }
        }

        &__phones {
            text-align: center;

            @include respond-to (md-down) {
                margin-bottom: 30px;
            }
        }

        &__phones-phone {
            font-size: 36px;
            font-family: $font-secondary;
            line-height: 1.2;

            @include respond-to (lg) {
               font-size: 28px;
            }

            @include respond-to (md) {
                font-size: 44px;
            }

            @include respond-to (xs) {
                font-size: vw(34);
            }

            a {
                color: $color-blue;
            }

            + .header__top__phones-phone {
                margin-top: 15px;
            }
        }
    }
}