.section-pluso {
    overflow: hidden;

    &__wrap {
        position: relative;

        &:before {
            content: '';
            position: absolute;
            top: 0;
            bottom: 44px;
            left: 0;
            right: 0;
            border: 22px solid $color-blue;

            @include respond-to (sm-down) {
                display: none;
            }
        }
    }

    &__item {
        display: flex;
        align-items: flex-end;
        margin-bottom: 44px;

        @include respond-to (sm) {
            position: relative;
        }

        &--1 .section-pluso__item__image img {
            margin: 0 -75px -40px -30px;
        }

        &--2 .section-pluso__item__image img {
            margin: 0 -75px -40px -30px;
        }

        &--3 .section-pluso__item__image img {
            margin: 0 -78px -40px -54px;
        }

        &--4 .section-pluso__item__image img {
            margin: 0 -75px -40px -30px;
        }

        &--5 .section-pluso__item__image img {
            margin: -7px -75px -40px -30px;
        }

        &--6 .section-pluso__item__image img {
            margin: 15px -77px -39px -42px;
        }

        &__image {
            img {
                max-width: initial;
            }

            @include respond-to (xs) {
                flex-basis: vw(127);
            }
        }

        &__title {
            font-size: 25px;
            font-family: $font-secondary;
            margin-bottom: 52px;

            @include respond-to (md-down) {
                font-size: 20px;
            }

            @include respond-to (sm) {
                position: absolute;
                bottom: 0;
                left: 50px;
                right: 0;
                text-align: right;
                text-shadow: 1px 1px 0 #FFF, -1px 1px 0 #FFF, 1px -1px 0 #FFF, -1px -1px 0 #FFF;
            }

            @include respond-to (xs) {
                font-size: 20px;
            }
        }
    }
}