.nav-top {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 15px 40px;

    @include respond-to (md-down) {
        padding-left: 0;
        padding-right: 0;
        margin-left: -15px;
        margin-right: -15px;
    }

    @include respond-to (xs) {
        position: absolute;
        background: rgba(#FFF, .85);
        z-index: 1;
        top: 110px;
        right: 0;
        left: 0;
        margin-left: 0;
        margin-right: 0;
        padding: 10px 0;
        flex-direction: column;
        display: none;
    }

    li {
        &.active {
            > a {
                text-decoration: underline;
            }
        }

        > a {
            color: #f2f2ff;
            font-size: 24px;
            text-shadow: 0 5px 5px rgba(#000, .35);
            display: block;
            padding: 5px;

            @include respond-to (md-down) {
                padding-left: 15px;
                padding-right: 15px;
            }

            @include respond-to (xs) {
                color: #000;
                text-shadow: none;
                font-size: vw(24);
                text-align: center;
            }
        }
    }
}

.js-sticky-fixed {
    background: rgba(0,0,0,.35);

    .nav-top {
        padding-bottom: 8px;
    }
}