.section-peoples {
    &__item {
        display: block;
        margin-bottom: 30px;
        color: #000;

        &__image {
            position: relative;
            width: 270px;
            height: 270px;
            border-radius: 50%;
            box-shadow: 0 11px 32px 10px rgba(#000, .13);
            overflow: hidden;
            margin-bottom: 14px;

            @include respond-to (lg) {
                width: 210px;
                height: 210px;
            }

            @include respond-to (md-down) {
                margin-left: auto;
                margin-right: auto;
            }

            @include respond-to (sm) {
                width: 240px;
                height: 240px;
            }

            @include respond-to (xs) {
                width: vw(240);
                height: vw(240);
            }

            img {
                max-width: initial;
                max-height: 100%;
                display: block;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }

        &__title {
            font-size: 25px;
            text-align: center;
            font-family: $font-secondary;
            line-height: 1.2;
            margin-bottom: 5px;

            @include respond-to (xs) {
                font-size: vw(20);
            }
        }

        &__descr {
            font-size: 24px;
            text-align: center;
            line-height: 1;

            @include respond-to (xs) {
                font-size: vw(16);
            }
        }
    }
}