.btn {
    font-family: $font-secondary;
    font-size: 24px;
    padding: 8px 30px 7px;
    line-height: 1.2;

    &-danger {
        background: $color-danger;

        &.btn-inverse {
            border: 3px solid $color-danger;
            color: $color-danger;
            background: #FFF;
        }
    }

    &-xl {
        font-size: 25px;
        padding: 16px 48px;
        box-shadow: 0 5px 13px 5px rgba(#000, .1);

        @include respond-to (xs) {
            font-size: vw(24);
        }
    }

    &-rounded {
        border-radius: 100px;
    }

    &-block {
        padding-left: 10px;
        padding-right: 10px;
    }
}