body {
    font-family: $font-primary;
    line-height: 1.35;
    overflow-x: hidden;
}

img {
    max-width: 100%;
}

a, .btn {
    transition: .15s;
}
a {
    color: $color-blue;
}

strong {
    font-weight: 800;
}

.mobile-buter {
    display: none;
    border: 2px solid #000;
    border-radius: 4px;
    position: absolute;
    top: 0;
    right: 15px;
    width: 70px;
    height: 70px;
    padding: 13px 8px;
    flex-direction: column;
    justify-content: space-between;

    > span {
        display: block;
        height: 8px;
        border-radius: 5px;
        background: #000;
    }

    @include respond-to (xs) {
        display: flex;
    }
}

@media (min-width: 1200px) {
    .container {
        max-width: 1200px;
    }
}