.section-sale {
    &__item {
        position: relative;

        &__image {
            box-shadow: 0 5px 13px 5px rgba(#000, .15);

            img {
                display: block;
                min-height: 300px;

                @include respond-to (xs) {
                    min-height: initial;
                }
            }
        }

        &__body {
            position: absolute;
            top: 30px;
            bottom: 30px;
            left: 30px;
            right: 30px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            @include respond-to (xs) {
                position: static;
            }
        }

        &__title {
            font-size: 24px;
            line-height: 1.2;

            @include respond-to (xs) {
                font-size: vw(18);
                margin-bottom: 20px;
            }
        }

        &__title-text {
            display: table;
            padding: 0 10px 6px;

            > span {
                display: inline-block;
                background: #FFF;
                padding: 0 6px 5px;
            }

            @include respond-to (xs) {
                padding: 0;
            }
        }

        &__action {

            @include respond-to (xs) {
                .btn {
                    padding-left: 10px;
                    padding-right: 10px;
                    width: 100%;
                }
            }
        }
    }
}