.section-when {
    &__item {
        display: block;
        color: #000;
        margin-bottom: 30px;

        &:hover {
            text-decoration: none;
            color: $color-blue;

            .section-when__item__image img {
                transform: scale(1.2);
            }
        }

        &__image {
            margin-bottom: 7px;
            border: 12px solid $color-blue;
            overflow: hidden;

            img {
                display: block;
                width: 100%;
                transition: .5s;
            }
        }

        &__title {
            font-size: 24px;
            text-align: center;
            padding-bottom: 12px;

            @include respond-to (xs) {
                font-size: vw(24);
            }
        }
    }
}