.section-form {
    &__wrap {
        position: relative;
        padding: 36px 0 0;

        @include respond-to (sm-down) {
            padding-top: 0;
        }

        &:before {
            content: '';
            position: absolute;
            border: 22px solid $color-blue;
            top: 0;
            bottom: 0;
            left: 70px;
            right: 0;

            @include respond-to (sm-down) {
                display: none;
            }
        }
    }

    &__title {
        color: #FFF;
        font-size: 90px;
        font-family: $font-secondary;
        text-shadow: 0 0 26px rgba(#000, .75);
        line-height: 1;

        @include respond-to (lg) {
            font-size: 74px;
            line-height: 1.4;
        }

        @include respond-to (md) {
            font-size: 70px;
            margin-bottom: 30px;
        }

        @include respond-to (sm-down) {
            text-shadow: none;
            color: $color-blue;
            font-size: 36px;
            margin-bottom: 28px;
            text-align: center;
        }

        @include respond-to (xs) {
            font-size: vw(30);
        }
    }

    &__form {
        padding-right: 50px;

        @include respond-to (md-down) {
            padding-left: 120px;
            margin-bottom: 30px;

            > div {
                margin-bottom: 15px !important;
            }
        }

        @include respond-to (sm-down) {
            padding-left: 0;
            padding-right: 0;
        }
    }

    &__action {
        position: relative;
        text-align: center;
        z-index: 1;

        .btn {
            margin-bottom: -52px;

            @include respond-to (md) {
                margin-left: 70px;
            }

            @include respond-to (xs) {
                padding-left: 10px;
                padding-right: 10px;
                width: 100%;
            }
        }
    }

    &__badge {
        color: #FFF;
        font-size: 24px;
        display: table;
        position: relative;
        margin-left: 73px;
        font-weight: 300;
    }
}