.section-gallery {
    &__item {
        display: block;
        margin-bottom: 30px;

        @include respond-to (sm-down) {
            margin-bottom: 20px;
        }
    }

    @include respond-to (sm) {
        .row {
            margin-left: -10px;
            margin-right: -10px;

            > [class*="col-"] {
                padding-left: 10px;
                padding-right: 10px;
            }
        }
    }
}