.modal {
    &-title {
        font-size: 36px;
        color: $color-blue;
        font-family: $font-secondary;
        text-align: center;
        margin-bottom: 30px;
        line-height: 1.2;
    }

    &-body {
        border: 22px solid $color-blue;
    }

    &-footer {
        background: none;
        border: none;
        padding: 0;
        position: absolute;
        bottom: -20px;
        left: 50%;
        transform: translateX(-50%);
    }
}